import axios from 'axios';
import Notiflix from 'notiflix';
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

document.getElementById('search-form').addEventListener('submit', function (e) {
  e.preventDefault();

  const searchQuery = e.target.elements.searchQuery.value;

  // Verificai dac cmpul de cutare nu este gol
  if (!searchQuery.trim()) {
    showNotification('Please enter a search query.');
    return;
  }

  // Apelai funcia pentru a face cererea ctre API
  searchImages(searchQuery);
});

let currentPage = 1;
let loadingMore = false;

window.addEventListener('scroll', () => {
  if (
    window.innerHeight + window.scrollY >= document.body.offsetHeight - 100 &&
    !loadingMore
  ) {
    loadingMore = true;
    loadMoreImages();
  }
});

function showNotification(message) {
  Notiflix.Notify.warning(message);
}

function searchImages(query, page = 1) {
  // Adugai cheia API Pixabay aici
  const apiKey = '40897523-298f69bbdc7791e1add26dc98';

  // Configurai parametrii pentru solicitarea HTTP
  const params = {
    key: apiKey,
    q: query,
    image_type: 'photo',
    orientation: 'horizontal',
    safesearch: true,
  };

  // Facei solicitarea HTTP folosind Axios
  axios
    .get('https://pixabay.com/api/', { params: { ...params, page } })
    .then(response => {
      const images = response.data.hits;
      const totalHits = response.data.totalHits;

      // Verificai dac exist imagini
      if (images.length === 0) {
        showNotification(
          'Sorry, there are no images matching your search query. Please try again.'
        );
        return;
      }

      // Aici putei manipula imaginile cum dorii, de exemplu, le putei aduga n galerie
      displayImages(images);

      // Adugai notificarea dup prima solicitare
      if (page === 1) {
        showNotification(`Hooray! We found ${totalHits} images.`);
      }

      loadingMore = false; // Setm loadingMore la false dup ce am primit rspunsul
    })
    .catch(error => {
      console.error('Error searching images:', error);
      showNotification(
        'An error occurred while searching for images. Please try again.'
      );
    });
}

function displayImages(images) {
  const gallery = document.querySelector('.gallery');

  // Curai galeria nainte de a aduga noi imagini
  gallery.innerHTML = '';

  images.forEach(image => {
    const card = createImageCard(image);
    gallery.appendChild(card);
  });

  // Dup adugarea cardurilor, actualizai Lightbox
  const lightbox = new SimpleLightbox('.gallery a');
  lightbox.refresh();

  // Afiai butonul "Load more" dac mai sunt imagini disponibile
  const loadMoreButton = document.querySelector('.load-more');

  if (loadMoreButton) {
    // Verificai dac exist butonul nainte de a ncerca s accesai proprietatea 'style'
    loadMoreButton.style.display = images.length < 40 ? 'none' : 'block';

    // Adugai eveniment pentru butonul "Load more"
    loadMoreButton.removeEventListener('click', loadMoreImages); // Eliminai orice event listener anterior
    loadMoreButton.addEventListener('click', loadMoreImages);
  }
}

// Adugai funcia pentru derularea fluid a paginii
function smoothScrollTo(element) {
  window.scroll({
    top: element.offsetTop,
    behavior: 'smooth',
  });
}

// n funcia loadMoreImages, dup ce ai primit rspunsul cu imaginile noi, adugai apelul pentru derulare fluid
function loadMoreImages() {
  currentPage++;
  const searchQuery =
    document.getElementById('search-form').elements.searchQuery.value;
  searchImages(searchQuery, currentPage);

  // Obinei ultimul card adugat i derulai fluid la el
  const gallery = document.querySelector('.gallery');
  const lastCard = gallery.lastElementChild;
  smoothScrollTo(lastCard);
}

function createImageCard(image) {
  const card = document.createElement('a'); // Folosim un element de ancor pentru Lightbox
  card.href = image.largeImageURL;
  card.className = 'photo-card';

  const imageElement = document.createElement('img');
  imageElement.src = image.webformatURL;
  imageElement.alt = image.tags;

  const info = document.createElement('div');
  info.className = 'info';

  const likes = document.createElement('p');
  likes.className = 'info-item';
  likes.innerHTML = `<b>Likes:</b> ${image.likes}`;
  info.appendChild(likes);

  const views = document.createElement('p');
  views.className = 'info-item';
  views.innerHTML = `<b>Views:</b> ${image.views}`;
  info.appendChild(views);

  const comments = document.createElement('p');
  comments.className = 'info-item';
  comments.innerHTML = `<b>Comments:</b> ${image.comments}`;
  info.appendChild(comments);

  const downloads = document.createElement('p');
  downloads.className = 'info-item';
  downloads.innerHTML = `<b>Downloads:</b> ${image.downloads}`;
  info.appendChild(downloads);

  card.appendChild(imageElement);
  card.appendChild(info);

  return card;
}
